<template>
    <div class="col-md-8 mx-auto mt-3">
        <form  @submit.prevent="updateContact()">
        <div class="form-group">
          <input
            v-model="contact.firstName"
            type="text"
            class="form-control"
            placeholder="Enter First Name here"
            id=""
          />
        </div>
        <div class="form-group">
          <input
            v-model="contact.lastName"
            type="text"
            class="form-control"
            placeholder="Enter Last Name here"
            id=""
          />
        </div>
        <div class="form-group">
          <input
            v-model="contact.phoneNumber"
            type="text"
            class="form-control"
            placeholder="Enter Phone Number here"
            id=""
          />
        </div>
        <button type="submit" class="btn btn-primary float-left ">
          Update contact
        </button>
      </form>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    name:"EditContact",
    components:{
    }
    ,
    data() {
        return {
           id:this.$route.params.id,
           contact:{
                 firstName:'',
                 lastName:'',
                 avatar:''  
                }
        }
    },
    methods: {
      async updateContact() {
        try {
          
        const res = await axios.put(`/api/contact/${this.id}`,{
          firstName:this.contact.firstName,lastName:this.contact.lastName,phoneNumber:this.contact.phoneNumber},{
          headers: {
            'auth-token': localStorage.getItem('auth-token'),
          },
          }
        )
        if (res) {
          this.$router.push('/contact')
          this.contact = res.data
        }
        } catch (error) {
          console.log(error)
        }
      }
      },
      computed: {
        avatar(){
          return this.contact.avatar
        }
      },
      mounted() {
          const Id =this.$route.params.id
          axios.get(`/api/contact/${Id}`,{headers:{
              'auth-token':localStorage.getItem('auth-token')
          }}).then((res)=>{
              this.contact = res.data[0]
          }).catch(err=>console.error(err))
      },
}
</script>
